module.exports = "import React from 'react';\nimport { Map } from '@joeattardi/react-mapquest-static-map';\n\nimport { MAPQUEST_API_KEY } from '../config';\n\nexport default function MapWithZoom() {\n  return (\n    <div>\n      <Map\n        apiKey={MAPQUEST_API_KEY}\n        center=\"Boston, MA\"\n        width={800}\n        height={500}\n        zoom={15}\n      />\n    </div>\n  );\n}\n"